export function generateDefaultAttributes() {

  (function ($, Drupal, drupalSettings) {
    const element_fields = drupalSettings.noahs_items_fields;


    const getWidgetsAttributes = async function () {
      const iframeEl = document.getElementById('noahs_page_builder_preview');
      if (!iframeEl || !iframeEl.contentDocument) return;
      const iframe = iframeEl.contentDocument;
      const widgets = iframe.querySelectorAll('.noahs_page_builder-widget');

      await Promise.all(Array.from(widgets).map(async (widget) => {
        applyClasses(widget);
      }));
    }

    const applyClassesFromIframe = function (widgetHtml) {
      Array.from(widgetHtml).forEach(child => {
        applyClasses(child);
      });

      return widgetHtml;
    }

    // Definir funciones en el mismo mbito del IIFE
    const applyClasses = function (widgetHtml) {

      const widgetId = widgetHtml.getAttribute('data-widget-id');
      const raw = widgetHtml.getAttribute('data-settings');
      let settings = {};
      try { settings = raw ? JSON.parse(raw) : {}; } catch (e) { settings = {}; }

      if (settings && settings.element.class) {
        let widgetFields = element_fields[settings.type] || [];
        const classes = settings.element.class;
        if (!classes) return widgetHtml;

        for (const [i, value] of Object.entries(classes)) {
          let fieldDef = widgetFields[i];
          let style_selector = fieldDef ? fieldDef.style_selector : null;
          let style_type = fieldDef ? fieldDef.style_type : null;
          if (style_type === 'class') {
            // agregar clase desde value al style_selector 
            if (style_selector) {
              style_selector = '#widget-id-' + widgetId + ' ' + style_selector;
              const targetElements = widgetHtml.querySelectorAll(style_selector);

              targetElements.forEach(el => {
                if(value.class != undefined) {
                  const classesToAdd = value.class.split(/\s+/);
                  classesToAdd.forEach(cls => el.classList.add(cls));
                }else{
                  //error si viene mas de una clase separada por espacio
                  const classesToAdd = value.split(/\s+/);
                  classesToAdd.forEach(cls => el.classList.add(cls));
                }
  
              });
            }
          }
        }
      }
      return widgetHtml;
    };
      
    const applyAttributes = function () {
      // Placeholder para atributos por defecto si es necesario.
    };

    // Registrar handler despus de definir las funciones
    $('#noahs_page_builder_preview').on('load', function () {
      getWidgetsAttributes();
    });

    window.applyClassesFromIframe = applyClassesFromIframe;

  })(jQuery, Drupal, drupalSettings);

}