export function clickRigthMenu() {

  (function ($, Drupal, drupalSettings) {

    document.getElementById('noahs_page_builder_preview').addEventListener('load', function () {

      // Obtener el contenido del iframe
      const iframeDocument = this.contentDocument || this.contentWindow.document;

      // Registrar el evento contextmenu dentro del iframe
      $(iframeDocument).on('contextmenu', '.noahs_page_builder-widget:not(.widget-noahs-settings)', function (e) {
        e.preventDefault();

        // Evitar que el evento se propague a elementos padres
        e.stopPropagation();

        // Eliminar cualquier men existente antes de crear uno nuevo
        $(iframeDocument).find('.noahs-context-menu').remove();

        // Obtener el tipo de widget
        const widgetType = $(this).data('widget-title');
        const widgetId = $(this).data('widget-id');


        // Crear el men sin Live Edit (solo bsico)
        const menu = $(
          `<div class="noahs-context-menu position-absolute bg-light border rounded shadow">
            <div class="menu-header p-2 border-bottom">
              <span class="fw-bold">${widgetType}</span>
              <button type="button" class="btn-close position-absolute top-0 end-0 m-2" aria-label="Close"></button>
            </div>
            <ul class="list-unstyled m-0 p-0">
              <li><div class="link noahs_page_builder-edit-widget" data-widget-id="${widgetId}">${Drupal.t('Edit')}</div></li>
              <li><div class="link noahs_page_builder-copy-id" data-widget-id="${widgetId}">${Drupal.t('Copy ID')}</div></li>
              <li><div class="link noahs_page_builder-remove-widget" data-widget-id="${widgetId}">${Drupal.t('Remove')}</div></li>
            </ul>
          </div>`
        );

        // Posicionar el men dentro del elemento
        $(this).append(menu);



        // Posicionar el men en el punto del clic
        menu.css({
          top: e.offsetY,
          left: e.offsetX,
        });

        // Cerrar el men al hacer clic fuera o en la X
        $(iframeDocument).on('click', function (event) {
          if (!$(event.target).closest('.noahs-context-menu').length) {
            $(iframeDocument).find('.noahs-context-menu').remove();
          }
        });

        menu.find('.btn-close').on('click', function () {
          menu.remove();
        }); 

        // Acciones de los botones
        menu.find('.link').on('click', function () {
          setTimeout(() => {
            menu.remove();
          }, 100);
        });

      });
    });


  })(jQuery, Drupal, drupalSettings);

}