export function draggableImage() {

  (function ($, Drupal, drupalSettings) {

    
    initMutationObserver();

    function initMutationObserver() {
      const observer = new MutationObserver((mutations) => {
        mutations.forEach((mutation) => {
          mutation.addedNodes.forEach((addedNode) => {
            if (addedNode.nodeType === 1 && addedNode.classList.contains("noahs_page_builder-modal")) {
              setTimeout(() => {
                initializeDraggable();
              }, 50);
            }
          });
        });
      });

      observer.observe(document.body, { childList: true, subtree: true });
    }

    function initializeDraggable() {
      let dropArea = $('.noahs-img-drop-area');

      // Evita que el navegador abra la imagen
      dropArea.on('dragover dragenter', function(e) {
        e.preventDefault();
        e.stopPropagation();
        dropArea.addClass('drag-over');
      });

      dropArea.on('dragleave dragend drop', function(e) {
        e.preventDefault();
        e.stopPropagation();
        dropArea.removeClass('drag-over');
      });

      // Cuando sueltan el archivo
      dropArea.on('drop', function(e) {

        let files = e.originalEvent.dataTransfer.files;
        if (!files || files.length === 0) {
          $('.modal-messages').append(Drupal.t('<p>Please, select at least one file</p>'));
          return;
        }

        var formData = new FormData();

        for (var i = 0; i < files.length; i++) {
          var fileInput = files[i];
          if (fileInput.type !== 'image/jpeg' && fileInput.type !== 'image/png' && fileInput.type !== 'image/gif') {
            $('.modal-messages').append(Drupal.t('<p>Please, select a JPG, PNG or GIF file</p>'));
            return;
          }

          formData.append('files[' + i + ']', fileInput);
        }

        // Capturar el contexto correcto
        var self = $(this);

        $.ajax({
          url: '/noahs-admin/noahs_page_builder/upload_file',
          type: 'POST',
          data: formData,
          processData: false,
          contentType: false,
          success: function(response) {
            // Cuando se suba el archivo Drupal devolver el fid, url, etc.
            let values = response.files[0];

            self.parent().find('.background-fid').val(values.media_id);
            self.parent().find('.background-thumbnail').val(values.file_url);
            self.parent().find('.background-url').val(values.file_fullurl);
            self.parent().find('.background-thumbnail-image').attr('src', values.file_url);
            var widget_id = document.querySelector('input[name="wid"]').value;
            let source = self.data('source');

            if (source === 'image') {
              var formJson = self.closest('form').find(':input').filter(function () {
                  return this.value.trim().length > 0;
              }).serializeJSON();
              regenerateWidget(widget_id, formJson);
            } else {
              modificarEstilo(self, widget_id); 
            }
            modificarJson(self, widget_id);
          }
        });
      });
    }

  })(jQuery, Drupal, drupalSettings);

}