/**
 * Exportar funciones
 *
 */



export function topPanel() {


  (function ($, Drupal, drupalSettings) { 

    $('#noahs_page_builder_preview').on('load', function() {
      initUpdateWidth();
      updateScaleIframe();
    });
    
    function initUpdateWidth(){
      const elemento = $('#noahs_page_builder_preview');
      const anchoTexto = $('#noahs-window-size input');
      const observer = new ResizeObserver(() => {
        if (!anchoTexto.is(':focus')) {
          actualizarAnchoElemento(elemento);
        }
      });
    
      observer.observe(elemento[0]);
      actualizarAnchoElemento(elemento); 
    
      anchoTexto.on("focus", function() {
        $(this).data('userEditing', true);
      });
    
      anchoTexto.on("blur", function() {
        $(this).data('userEditing', false);
        actualizarAnchoElemento(elemento);
      });
    
      anchoTexto.on("change keyup", function() {
        const nuevoAncho = $(this).val(); 
        $('.noahs_page_builder_preview-content').width(nuevoAncho); // Cambia el ancho del iframe dinmicamente
      });
    }
        
    function actualizarAnchoElemento(elemento) {
      const anchoTexto = $('#noahs-window-size input');
      if (!anchoTexto.data('userEditing')) {
        const ancho = elemento.width();
        anchoTexto.val(ancho);
      }
    }
    
    function updateScaleIframe(){
      $('#noahs-window-scale input').on("change keyup", function() {
        const valor = $(this).val(); 
        if (isNaN(valor) || valor < 20) valor = 20;
        if (valor > 100) valor = 100;
      
        let escala = valor / 100; // Normaliza el valor
        $('#noahs_page_builder_preview').css({
          'transform': `scale(${escala})`,
          'transform-origin': 'top center'
        });
      });
    }

    
  })(jQuery, Drupal, drupalSettings);

}