



export function noahsUrlAutocomplete() {
  (function ($, Drupal, drupalSettings) {
  initMutationObserver();


  function initMutationObserver() {

    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        mutation.addedNodes.forEach((addedNode) => {
          if (addedNode.nodeType === 1 && addedNode.classList.contains("noahs_page_builder-modal")) {
            setTimeout(() => {
              if ($(addedNode).find(".url_autocomplete").length) {
                urlAutocomplete();
              }
            }, 50);
          }
        });
      });
    });

    observer.observe(document.body, { childList: true, subtree: true });
  }

  function urlAutocomplete(){
    $(".url_autocomplete").on('change', function () {
      const value = $(this).val();
      let isValidUrl = false;
      try {
        new URL(value);
        isValidUrl = true;
      } catch (_) {
        isValidUrl = false;
      }
      if (isValidUrl) {
        $(this).parent().find('.node_id').val('');
      }
    });

    $(".url_autocomplete").autocomplete({
      source: function (request, response) {
        $.ajax({
          url: "/noahs-admin/nohas-url-autocomplete",
          type: "GET",
          dataType: "json",
          data: {
            q: request.term
          },
          success: function (data) {
            // data es [{text, children: [{id, text}]}]
            let flat = [];
            data.forEach(function(group) {
              if (group.children && group.children.length) {
                // Aadir cabecera de grupo (no seleccionable)
                flat.push({ label: group.text, value: '', isGroup: true, disabled: true });
                group.children.forEach(function(item) {
                  flat.push({ label: '' + item.text, value: item.id });
                });
              }
            });
            response(flat);
          }
        });
      },
      focus: function(event, ui) {
        // No seleccionar cabeceras de grupo
        if (ui.item.isGroup) {
          event.preventDefault();
        }
      },
      select: function (event, ui) {
        if (ui.item.isGroup) {
          event.preventDefault();
          return false;
        }
        var label = ui.item.label;
        var value = ui.item.value;
        $(this).val(label.replace(/^\s+/, ''));
        $(this).parent().find('.node_id').val(value);
        $('.url_autocomplete').trigger('change');
        return false;
      },
      minLength: 2
    }).autocomplete('instance')._renderItem = function(ul, item) {
      if (item.isGroup) {
        return $('<li class="ui-autocomplete-group"></li>')
          .append('<div style="font-weight:bold;">' + item.label + '</div>')
          .appendTo(ul);
      } else {
        return $('<li></li>')
          .append('<div>' + item.label + '</div>')
          .appendTo(ul);
      }
    };
  }
})(jQuery, Drupal, drupalSettings);
}
        
