export function saveData() {

  window.noahsSavePage = noahsSavePage; 

  jQuery('#noahs_page_builder_preview').on('load', function() {
    this.contentWindow.noahsSavePage = noahsSavePage;
  });

  // Manejar el evento de envo del formulario
  document.addEventListener("submit", handleSubmit, false);

  // Atajo de teclado para guardar (Ctrl+S o Command+S)
  document.addEventListener('keydown', function (e) {
    // Detectar si es Mac
    let isMac = navigator.platform.toUpperCase().indexOf('MAC') >= 0;

    // Command+S (Mac) o Ctrl+S (Windows/Linux)
    if ((isMac && e.metaKey && e.key === 's') || (!isMac && e.ctrlKey && e.key === 's')) {
      e.preventDefault(); 
      noahsSavePage(); 
    }
  });

  function handleSubmit(e) {
    e.preventDefault();
    
    const form = e.target;
    const submitter = e.submitter;
    let formData = getFormSettings(submitter);

    if (form.matches('#noahs_page_builder_edit_widget_form')) {
      if (submitter.classList.contains('save-widget')) {
        noahsSavePage();
      } else if (submitter.classList.contains('save-global-widget')) {
        saveGlobal(formData);
      }
    } else if (form.matches('#noahs_page_builder_sttings_page_form') || form.matches('#noahs_page_builder_form_builder')) {
      noahsSavePage();
    }else if(form.matches('#noahs_settings_form')){
      noahsSaveSettings(formData); 
    }
  } 

  async function noahsSaveSettings(formData) {


    let css = await generateWidgetStyles(formData);
    let settings = formData;


    const data = {    
        entity_id: 'noahs_settings',
        uid: drupalSettings.uid,
        language: 'no_language',
        noahs_id: 'noahs_settings',
        entity_type: 'noahs_settings',
        settings: JSON.stringify(settings),
        css: css
    };

    jQuery.ajax({
      url: drupalSettings.savePage,
      type: 'POST',
      data: JSON.stringify(data),
      dataType: 'json',
      contentType: 'application/json',
      headers: {
        'X-CSRF-Token': drupalSettings.csrfToken
      },
        success: function (return_data) {  
          createAlert('', 'Noahs settings saved!', 'everything went well.', 'info', true, true, 'pageMessages');   
        },
        error: function (jqXHR, textStatus, errorThrown) {
            console.log(textStatus);
            console.log(errorThrown);
            createAlert('Opps!',textStatus + ":" +  'Something went wrong','Check your drupal Recent log messages.','danger',true,false,'pageMessages');
        }
    });

  }

  async function noahsSavePage() {

    let css = await prepareStyles();
    css += await prepatePageStyles();
    var settings = getWidgetStructure();
    var pageSettings = {
      'page_settings': jQuery('#noahs_page_builder_sttings_page_form').serializeJSON(),
      'page_fields': prepateFormPageSettings()
    }

    var data_id = drupalSettings.noahs_id;

    var data = {    
        entity_id: data_id,
        noahs_id: data_id,
        uid: drupalSettings.uid,
        langcode: drupalSettings.langcode,
        entity_type: drupalSettings.entity_type,
        settings: settings,
        page_settings: JSON.stringify(pageSettings),
        css: css
    };

    if(drupalSettings.theme_builder){
        data_id = drupalSettings.theme_builder_type;
        data = {    
            type: data_id, 
            langcode: drupalSettings.langcode,
            settings: settings,
            page_settings: JSON.stringify(pageSettings),
            css: css
        };
    }

    jQuery.ajax({
      url: drupalSettings.savePage,
      type: 'POST',
      data: JSON.stringify(data),
      dataType: 'json',
      contentType: 'application/json',
      headers: {
        'X-CSRF-Token': drupalSettings.csrfToken
      },
      success: function (return_data) {
        createAlert('', 'Noahs settings saved!', 'everything went well.', 'info', true, true, 'pageMessages');
      },
      error: function (jqXHR, textStatus, errorThrown) {
        console.log(textStatus);
        console.log(errorThrown);
        createAlert('Opps!', textStatus + ":" + 'Something went wrong', 'Check your Drupal Recent log messages.', 'danger', true, false, 'pageMessages');
      }
    });

  }


}