




export function select2Init() {
  (function ($, Drupal, drupalSettings) {


  initMutationObserver();


  function initMutationObserver() {

    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        mutation.addedNodes.forEach((addedNode) => {
          if (addedNode.nodeType === 1 && addedNode.classList.contains("noahs_page_builder-modal")) {
            setTimeout(() => {
              initSelect2();
            }, 50);
          }
        });
      });
    });

    observer.observe(document.body, { childList: true, subtree: true });
  }

  function initSelect2(){

      $('[data-select-2="1"]').select2({
          width: 'resolve',
          theme: "classic"
      });

      $('[data-select-2-sortable]').each(function () {

        var $this = $(this); // Corrige la referencia a `$this`
        var oldValues = $this.data('select-value'); // Obtn el atributo data-select-value como un array
      
        if (Array.isArray(oldValues)) {
    
          // Ordena las opciones del select segn los valores de data-select-value
          var options = $this.find('option');
          options.sort(function (a, b) {
            var indexA = oldValues.indexOf($(a).val());
            var indexB = oldValues.indexOf($(b).val());
            return (indexA === -1 ? Infinity : indexA) - (indexB === -1 ? Infinity : indexB);
          });
          $this.empty().append(options); // Limpia y reordena las opciones en el select
        }
      
        // Inicializa Select2
        var selectEl = $this.select2({ width: '100%' });
      
        // Hacer las opciones de Select2 arrastrables
        selectEl.next().find('ul').sortable({
          containment: 'parent',
          stop: function (event, ui) {
            console.log('caca');
            ui.item.parent().children('[title]').each(function () {
              var title = $(this).attr('title');
              $('option:contains(\'' + title + '\')', selectEl).filter(function () {
                if ($(this).text() === title) {
                  var original = $(this);
                  original.detach();
                  selectEl.append(original);
                }
              });
            });
            selectEl.change(); // Dispara el evento de cambio en el select
          }
        });
      });
      
      

  };

  })(jQuery, Drupal, drupalSettings);

  }