/**
 * history functions (parent window)
 */
import "jquery-ui/ui/widgets/sortable";


export function history() {
  (function ($, Drupal, drupalSettings) {
    let history = [];
    let currentIndex = -1;
    let hasUnsavedChanges = false; // Variable para rastrear cambios

    // Botones
    let backButton = document.getElementById("backButton");
    let forwardButton = document.getElementById("forwardButton");

    if (backButton && forwardButton) {
      backButton.classList.add('disabled');
      forwardButton.classList.add('disabled');
      backButton.addEventListener("click", goBack);
      forwardButton.addEventListener("click", goForward);
    }

    function updateHistoryButtons() {
      if (!backButton || !forwardButton) return;

      backButton.disabled = currentIndex <= 0;
      forwardButton.disabled = currentIndex >= history.length - 1;

      backButton.classList.toggle('disabled', currentIndex <= 0);
      forwardButton.classList.toggle('disabled', currentIndex >= history.length - 1);
    }

    // Escuchar mensajes desde el iframe
    window.addEventListener("message", (event) => {
      if (!event.data) return;

      if (event.data.type === "contentChanged") {
        const newState = event.data.content ?? "";

        // Si no hay historial inicializado, este es el estado inicial
        if (currentIndex === -1) {
          history.push(newState);
          currentIndex = 0;
          updateHistoryButtons();
          return;
        }

        // Evitar duplicados (si el estado actual es distinto aadimos)
        if (history[currentIndex] !== newState) {
          history = history.slice(0, currentIndex + 1); // si bamos "en medio", cortamos el forward
          history.push(newState);
          currentIndex = history.length - 1;
          hasUnsavedChanges = true; // Marcar cambios
          updateHistoryButtons();
        }
      }
    });

    // Advertencia al intentar salir si hay cambios
    window.addEventListener("beforeunload", (event) => {
      if (hasUnsavedChanges) {
        event.preventDefault();
        event.returnValue = "Tienes cambios sin guardar. Ests seguro de que deseas salir?";
      }
    });

    // Pedimos al iframe el estado inicial (por si todava no lo ha enviado)
    const iframe = document.getElementById("noahs_page_builder_preview");
    if (iframe && iframe.contentWindow) {
      try {
        iframe.contentWindow.postMessage({ type: "requestInitialState" }, "*");
      } catch (err) {
        // cross-origin o similar: en ese caso dependemos de que el iframe nos envie el estado cuando est listo
        console.warn('No se pudo pedir estado inicial al iframe:', err);
      }
    }

    // Ir atrs
    function goBack() {
      if (currentIndex > 0) {
        currentIndex--;
        restoreState(history[currentIndex]);
        updateHistoryButtons();
      }
    }

    // Ir adelante
    function goForward() {
      if (currentIndex < history.length - 1) {
        currentIndex++;
        restoreState(history[currentIndex]);
        updateHistoryButtons();
      }
    }

    // Restaurar estado (enva al iframe)
    function restoreState(content) {
      const iframe = document.getElementById("noahs_page_builder_preview");
      if (iframe && iframe.contentWindow) {
        iframe.contentWindow.postMessage({ type: "restoreState", content }, "*");
      }
      iframe.contentWindow.initializeSortable();
      // actualiza UI del padre tras restaurar (si tienes esas funciones disponibles)
      setTimeout(() => {
        if (typeof loadMenu === 'function') loadMenu();
        if (typeof applyWidgetStyles === 'function') applyWidgetStyles();
        $("#noahs_page_builder .noahs_page_builder-widget ").sortable("refresh");

      }, 10);
    }
  })(jQuery, Drupal, drupalSettings);
}
