


export function updateCss() {
  (function ($, Drupal, drupalSettings) {

    window.modificarEstilo = modificarEstilo;
    window.modifyPageStyles = modifyPageStyles;
    window.modificarJson = modificarJson;
    window.defaultData = defaultData;
    window.getFormSettings = getFormSettings;
    window.modifyNoahsSettingstyles = modifyNoahsSettingstyles;
    window.updateFromSelector = updateFromSelector;
 
    $('#noahs_page_builder_preview').on('load', function () {
      window.defaultData = defaultData;
      this.contentWindow.defaultData = defaultData;
      if ($('.trigger-settings-action').length) {
        modifyNoahsSettingstyles($('.trigger-settings-action'));
      } 
    });

    initMutationObserver(); 

    $(document).ready(function () {
      updateFromSelector('#noahs_page_builder_sttings_page_form');
      updateFromSelector('#noahs_settings_form');
    });

    function initMutationObserver() {
      const observer = new MutationObserver((mutations) => {
        mutations.forEach((mutation) => {
          mutation.addedNodes.forEach((addedNode) => {
            if (addedNode.nodeType === 1 && addedNode.classList.contains("noahs_page_builder-modal")) {
              setTimeout(() => {
                updateFromSelector('#noahs_page_builder_edit_widget_form');
              }, 50);
            }
          });
        });
      });

      observer.observe(document.body, { childList: true, subtree: true });
    }

    function updateFromSelector(formSelector) {
      $(formSelector).on("mousedown", ".noahs-input--control-css", function (e) {

        $(this).each(function (index) {
          const widget_id = $(this).closest('form').find('input[name="wid"]').val();
          var styleStype = $(this).data('style-type');
          var styleSelector = $(this).data('style-selector');
          var attributeType = $(this).data('attribute-type');
    
          if (styleSelector != undefined) {
            $(this).find('input, select, :checkbox, :radio').each(function (index, element) {
              if (!element) return;
    
              var localStyleSelector = styleSelector;
    
              if ($(e.delegateTarget).prop('id') === 'noahs_page_builder_edit_widget_form') {
                if (localStyleSelector === 'widget') {
                  localStyleSelector = '#widget-id-' + widget_id;
                } else if (!localStyleSelector.includes('#widget-id-' + widget_id)) {
                  localStyleSelector = '#widget-id-' + widget_id + ' ' + localStyleSelector;
                }
              }
    
              var targetElement = $('#noahs_page_builder_preview').contents().find(localStyleSelector);
              var stringClass = targetElement.attr('class');
              var selectedValue = $(this).is('select') ? $(this).val() : null;
    
              $(this).on('change keyup', function () {
    
                if (styleStype == 'class') {
                  var targetElement = $('#noahs_page_builder_preview').contents().find(localStyleSelector);
    
                  if ($(this).attr('type') === 'checkbox') {
                    $(this).is(':checked') ? targetElement.addClass($(this).val()) : targetElement.removeClass($(this).val());
                  } else if ($(this).is('select')) {
                    targetElement.removeClass(selectedValue);
                    targetElement.addClass($(this).val());
                  } else {
                    if (stringClass != '') {
                      targetElement.attr('class', stringClass + ' ' + $(this).val());
                    }
                  }
    
                  if ($(this).is("[name='element[class][gallery_type_columns]']")) {
                    targetElement.attr('class', 'row ' + $(this).val());
                  }
    
                } else if (styleStype == 'attribute') {
                  var targetElement = $('#noahs_page_builder_preview').contents().find(localStyleSelector);
                  var attributeValue = $(this).val();
                  if (attributeValue) {
                    targetElement.attr(attributeType, attributeValue);
                  }
                  updateIframeFunctions(targetElement);
                }
    
                if ($(e.delegateTarget).prop('id') === 'noahs_page_builder_edit_widget_form') {
                  if ($(element).hasClass('noahs-select-image-style')) {
                    let image_style = $(element).val();
                    let fid = $(element).closest('.noahs-input--control-css').find('.background-fid').val();
                    let mediaType = $(element).closest('.noahs-input--control-css').find('.background-media-type').val();

                    $.ajax({
                      url: `/noahs-admin/get-media-image/${mediaType}/${fid}/${image_style}`,
                      type: 'GET',
                      processData: false,
                      contentType: false,
                      success: function (data) {
                        $(element).closest('.noahs-input--control-css').find('.background-url').val(data);
                        modificarEstilo(element, widget_id);
                        modificarJson(element, widget_id);
                      }
                    });
                  } else {
                    modificarEstilo(element, widget_id);
                    modificarJson(element, widget_id);
                  }
                }
    
                if ($(e.delegateTarget).prop('id') === 'noahs_page_builder_sttings_page_form') {
                  modifyPageStyles();
                }
    
                if ($(e.delegateTarget).prop('id') === 'noahs_settings_form') {
                  modifyNoahsSettingstyles(element);
                }
              });
            });
    
          } else {
            $(this).find('input, select, :checkbox, :radio, textarea').each(function (index, element) {
              $(this).on('change keyup', function () {
                if ($(e.delegateTarget).prop('id') === 'noahs_page_builder_edit_widget_form') {
                  modificarJson(element, widget_id);
                }
              });
            });
          }
        });
      });
    }
    



    function modificarJson(element, widgetId) {

      const iframe = document.querySelector("#noahs_page_builder_preview").contentDocument;
      const settings = getFormSettings(element);
      const widget = iframe.querySelector(`#widget-id-${widgetId}`);
      if (widget) {
        widget.setAttribute("data-settings", JSON.stringify(settings));
      }
    }

    function getFormSettings(element) {

      var settings = $(element).closest('form').find(':input').filter(function () {
        if (!$(this).closest('.noahs-input--control-css').data('skip-field')) {
          return this.value.trim().length > 0;
        }
      }).serializeJSON();

      return settings

    }

    async function modificarEstilo(element, widgetId) {

      const settings = getFormSettings(element);
      const styles = await generateWidgetStyles(settings);

      let iframe = document.getElementById('noahs_page_builder_preview').contentDocument;
      let styleTag = iframe.getElementById(`w_style_${widgetId}`);
      if (!styleTag) {
        styleTag = iframe.createElement("style");
        styleTag.id = `w_style_${widgetId}`;
        iframe.head.appendChild(styleTag);
      }
      styleTag.innerHTML = styles;
    }

    async function modifyPageStyles() {

     applyPageStyles();

    }

    async function modifyNoahsSettingstyles(element) {
      const settings = getFormSettings(element);
      const styles = await generateWidgetStyles(settings);
      let iframe = document.getElementById('noahs_page_builder_preview').contentDocument;
      let styleTag = iframe.getElementById('w_style_no_id');

      if (!styleTag) {
        styleTag = iframe.createElement("style");
        styleTag.id = 'w_style_no_id';
        iframe.head.appendChild(styleTag);
      }

      styleTag.innerHTML = styles;
    }

    async function defaultData(settings, widgetId) {

      const styles = await generateWidgetStyles(settings);

      let iframe = document.getElementById('noahs_page_builder_preview').contentDocument;
      let styleTag = iframe.getElementById(`w_style_${widgetId}`);
      if (!styleTag) {
        styleTag = iframe.createElement("style");
        styleTag.id = `w_style_${widgetId}`;
        iframe.head.appendChild(styleTag);
      }
      styleTag.innerHTML = styles;
    }

  })(jQuery, Drupal, drupalSettings);

}