export function exportGenerateCss() {
  
  (function ($, Drupal, drupalSettings) {

    const element_fields = drupalSettings.noahs_items_fields;
    const page_fields = drupalSettings.noahs_page_fields;
    const viewport_tablet = drupalSettings.noahs_page_builder.viewport_tablet;
    const viewport_mobile = drupalSettings.noahs_page_builder.viewport_mobile;
    const palleteColors = drupalSettings.noahs_page_builder?.pallete_color || [];
    const variables_color = drupalSettings.noahs_page_builder?.variables_color || [];


    window.generateWidgetStyles = generateWidgetStyles;
    window.prepatePageStyles = prepatePageStyles;
    window.prepateFormPageSettings = prepateFormPageSettings;
    window.prepareStyles = prepareStyles;
    window.obtenerThemeCss = obtenerThemeCss;

    $('#noahs_page_builder_preview').on('load', function () {
      this.contentWindow.generateWidgetStyles = generateWidgetStyles;
      window.applyWidgetStyles();
      window.applyPageStyles();
    });

    /*  Funcin para aplicar estilos a los widgets */
    window.applyWidgetStyles = async function () {

      let iframe = document.getElementById('noahs_page_builder_preview').contentDocument;
      const widgets = iframe.querySelectorAll('.noahs_page_builder-widget');

      await Promise.all(Array.from(widgets).map(async (widget) => {

        const widgetId = widget.getAttribute("data-widget-id");
        const settings = JSON.parse(widget.getAttribute("data-settings") || null);

        if (settings) {

          const styles = await generateWidgetStyles(settings);
          let styleTag = iframe.createElement("style");

          styleTag.id = `w_style_${widgetId}`;
          iframe.head.appendChild(styleTag);
          styleTag.innerHTML = styles;
        }
      })); 
    }

    /*  Funcin para aplicar estilos de las pginas */
    window.applyPageStyles = async function () {
      let iframe = document.getElementById('noahs_page_builder_preview').contentDocument;

      const styles = await prepatePageStyles();
      if (styles) {
        let styleTag = iframe.getElementById('w_style_page_styles');

        if (!styleTag) {
          styleTag = iframe.createElement("style");
          styleTag.id = 'w_style_page_styles';
          iframe.head.appendChild(styleTag);
        }

        styleTag.innerHTML = styles;
      }
    }

    /*  Funcin para aplicar estilos de las pginas */
    async function prepatePageStyles() { 

      const form = document.querySelector("#noahs_page_builder_sttings_page_form");

      if (form) {

        const settings = $(form).find(':input').filter(function () {
          return this.value.trim().length > 0;
        }).serializeJSON();
        if (settings) {
          const styles = await generatPageStyles(settings, page_fields);
          return styles;
        }
      }
    }

    /*  Funcin para obtener los estilos de la pgina */
    function prepateFormPageSettings() {
      const fields = {};
      const form = document.querySelector("#noahs_page_builder_sttings_page_form");
      form.querySelectorAll("[data-control-type][data-control-id]").forEach(el => {
        const type = el.getAttribute("data-control-type");
        const id = el.getAttribute("data-control-id");
        const styleType = el.getAttribute("data-style-type");
        const styleSelector = el.getAttribute("data-style-selector");
        const styleCss = el.getAttribute("data-style-css");
        fields[id] = {
          type,
          style_type: styleType,
          style_selector: styleSelector,
          style_css: styleCss
        };
      });

      return fields;
    }

    /*  Funcin para aplicar estilos de los widgets */
    function prepareStyles() {
      // var items = drupalSettings.noahs_items;
      let values = window.getWidgetStructure();
      const page_settings = JSON.parse(values);
      const elements = obtenerElementosRecursivos(page_settings);

      return processElements(elements, element_fields);

    }

    function obtenerElementosRecursivos(elements) {
      let elementos = [];

      elements.forEach(value => {
        if (value.hasOwnProperty("settings") && typeof value.settings === "object") {
          elementos.push(value.settings);
        }

        if (value.hasOwnProperty("elements") && Array.isArray(value.elements)) {
          elementos = elementos.concat(obtenerElementosRecursivos(value.elements));
        }

        if (value.hasOwnProperty("columns") && Array.isArray(value.columns)) {
          elementos = elementos.concat(obtenerElementosRecursivos(value.columns));
        }
      });

      return elementos;
    }

    function processElements(elements, element_fields) {

      let promises = elements.map(async (item) => {

        if (element_fields && item.element && Object.keys(item.element)?.length) {

          const css = obtenerCss(item.element);

          if (css) {
            let item_type = item.type;
            return getStyles(element_fields[item_type], css, item.wid);
          }
        }
        return ""; // En caso de que no haya CSS
      });

      return Promise.all(promises).then((results) => {
        const output = results.join("\n");
        return output;
      });
    }

    /* Funcin para obtener el CSS de un elemento */
    function obtenerCss(element) {

      const cssArray = [];
      if (element.css && element.css != 'undefined') {
        cssArray.push(element.css);
      }
      const unifiedArray = mergeDeep(...cssArray);

      return unifiedArray;
    }

    function mergeDeep(...objects) {
      return objects.reduce((acc, obj) => {
        Object.keys(obj).forEach(key => {
          if (typeof obj[key] === 'object' && obj[key] !== null) {
            acc[key] = mergeDeep(acc[key] || {}, obj[key]);
          } else {
            acc[key] = obj[key];
          }
        });
        return acc;
      }, {});
    }

    async function getStyles(items, value, wid) {
      let css = "";


      if (value && Object.keys(value).length > 0) {
        for (const [query, item_value] of Object.entries(value)) {
          if (query === "desktop") {
            for (const [state, state_value] of Object.entries(item_value)) {

              css += await generateCss(state, state_value, items, wid);
            }
          }
          else if (query === "tablet") {
            css += "@media (max-width: " + viewport_tablet + "px) {";
            for (const [state, state_value] of Object.entries(item_value)) {
              css += await generateCss(state, state_value, items, wid);
            }
            css += "}";
          }
          else if (query === "mobile") {
            css += "@media (max-width: " + viewport_mobile + "px) {";
            for (const [state, state_value] of Object.entries(item_value)) {
              css += await generateCss(state, state_value, items, wid);
            }
            css += "}";
          }
        }
      }

      return css;
    }



    async function generateCss(state, state_value, items, wid) {
      let css = '';
      let selectors = {};

      for (const [itemId, element] of Object.entries(state_value)) {

        /** Multielements form styles */
        if (items[itemId]?.type === 'noahs_multiple_elements' && element) {
          let index = 1;

          for (const [k, style_css] of Object.entries(element)) {
            for (const [sk, _css] of Object.entries(style_css)) {

              let selector = '';
              let numero = sk.match(/_(\d+)$/)?.[1] || '';

              selector = items[itemId].fields[k].style_selector === 'widget'
                ? `#widget-id-${wid}`
                : `#widget-id-${wid} ${items[itemId].fields[k].style_selector}`;

              selector = state === 'hover' ? `${selector}:hover` : selector;
              selector = selector.replace('[index]', numero);

              let declaration = '';

              if (['noahs_font', 'noahs_margin', 'noahs_padding', 'noahs_radius', 'noahs_border'].includes(items[itemId].fields[k].type)) {
                for (const [property, item_value] of Object.entries(element[k])) {
                  if (property !== 'input_linked') {
                    if (item_value) {
                      // Verificar si el valor no tiene una unidad y agregar 'px' por defecto
                       if (!isNaN(item_value)) {
                         item_value = `${item_value}px`;
                       }
                      declaration += `${property.replace(/_/g, '-')}: ${item_value}; `;
                    }
                  }
                }
              }

              if (['select', 'noahs_color', 'checkbox', 'noahs_width'].includes(items[itemId].fields[k].type)) {
                declaration += `${items[itemId].fields[k].style_css}: ${_css}; `;
              } 

              if (items[itemId].fields[k].type === 'text' && items[itemId].fields[k].style_css?.text) {
                declaration += `${items[itemId].fields[k].style_css.text}: ${_css}; `;
              }

              if (items[itemId].fields[k].type === 'noahs_background_image') {
                try {
                  declaration += await getBackgroundImage(_css);
                } catch (error) {
                  console.error('Error obteniendo el fondo:', error);
                }
              }

              if (items[itemId].fields[k].type === 'noahs_background_gradient') {
                let background = getBackgroundGradient(element[k]);
                if (background) declaration += background;
              }

              if (items[itemId].fields[k].type === 'noahs_background_overlay') {
                let background = getBackgroundOverlay(_css);
                if (background) declaration += background;
              }

              if (declaration) {
                selectors[selector] = selectors[selector] || [];
                selectors[selector].push(declaration);
              }
            }
            index++;
          }
        }

        if (items[itemId]?.style_type === 'style') {

          let declaration = '';
          let selector = '';
          let resultado = '';

          if (element && items[itemId]?.style_selector) {

            if (items[itemId].style_selector.includes(',')) {
              let parts = items[itemId].style_selector.split(',');
              resultado = parts.map(el => `#widget-id-${wid} ${el}`).join(', ');

              if (resultado.includes('#widget-id-no_id')) {
                resultado = resultado.replace(/#widget-id-no_id\s*/g, '');
              }

              selector = resultado;
            } else {
              selector = items[itemId].style_selector.includes('[widget]')
                ? items[itemId].style_selector.replace('[widget]', `#widget-id-${wid}`)
                : items[itemId].style_selector === 'widget'
                  ? `#widget-id-${wid}`
                  : `#widget-id-${wid} ${items[itemId].style_selector}`;
              if (selector.includes('#widget-id-no_id')) {
                selector = selector.replace(/#widget-id-no_id\s*/, '');
              }
            }

            selector = selector.replace(/\s*:\s*before/g, ':before').replace(/\s*:\s*after/g, ':after');

            if (state === 'hover') {
              selector = selector
                .split(',')
                .map(part => `${part}:hover`)
                .join(', ');
            }

            if (items[itemId].style_selector_hover) {
              selector = `#widget-id-${wid} ${items[itemId].style_selector_hover}, ${selector}`;
            }

            if (['noahs_margin', 'noahs_padding', 'noahs_radius', 'noahs_border'].includes(items[itemId].type)) {
              for (let [property, value] of Object.entries(element)) {
                if (property !== 'input_linked') {
                  if (value) {
                    // Verificar si el valor es un nmero y agregar 'px' por defecto
                    if (!isNaN(value)) {
                      value = `${value}px`;
                    }
                    declaration += `${property.replace(/_/g, '-')}: ${value}; `;
                  }
                }
              }
            }

            if (['noahs_font'].includes(items[itemId].type)) {

              for (let [property, value] of Object.entries(element)) {
                if (value) {
                  if(property === 'color'){
                    const find_varible = Object.values(variables_color).includes(value);
                    if(find_varible) {
                      const varible = Object.entries(variables_color).find(([k, v]) => v === value)?.[0];
                      declaration += `${property.replace(/_/g, '-')}: var(${varible}); `;
                    }else{
                      declaration += `${property.replace(/_/g, '-')}: ${value}; `;
                    }
                  }else{
                    declaration += `${property.replace(/_/g, '-')}: ${value}; `;
                  }
                }
              }
            }

            if (items[itemId].type === 'noahs_shadows') {
              let { shadow_x = '0', shadow_y = '0', blur = '0', spread = '0', color = 'black' } = element;
              declaration += `box-shadow: ${shadow_x} ${shadow_y} ${blur} ${spread} ${color}; `;
            }

            if (items[itemId].type === 'noahs_coordinates') {
              for (let key in items[itemId].fields) {
                let property = key.replace(/_/g, '-');
                if (items[itemId].fields[key] && element[key]) {
                  declaration += `${property}: ${element[key]}; `;
                }
              }
            }

            // If the type is select, checkbox or noahs_width, add the style_css
            if (['select', 'number', 'checkbox', 'noahs_width'].includes(items[itemId].type)) {
              let styleSuffix = items[itemId].style_suffix || '';
              declaration += `${items[itemId].style_css}: ${element}${styleSuffix}; `;
            }

            // If the type is noahs_color, check if the element is a variable
            if ([ 'noahs_color'].includes(items[itemId].type)) {
              const find_varible = Object.values(variables_color).includes(element);
              if(find_varible) {
                const varible = Object.entries(variables_color).find(([k, v]) => v === element)?.[0];
                declaration += `${items[itemId].style_css}: var(${varible}); `;
              }else{
                declaration += `${items[itemId].style_css}: ${element}; `;
              }
            }

            if (items[itemId].type === 'text') {
              let styleSuffix = items[itemId].style_suffix || '';
              if (items[itemId].style_css && element.text) {
                declaration += `${items[itemId].style_css}: ${element.text}${styleSuffix}; `;
              }
            }

            if (items[itemId].type === 'noahs_image_mask' && element.mask) {
              declaration += `mask: url(${element.mask}) no-repeat center; -webkit-mask: url(${element.mask}) no-repeat center; mask-size: ${element.mask_size || '100%'}; -webkit-mask-size: ${element.mask_size || '100%'}; `;
            }

            if (items[itemId].type === 'noahs_transform') {
              let transforms = [];
              if (element.scale) transforms.push(`scale(${element.scale})`);
              if (element.rotate_x) transforms.push(`rotateX(${element.rotate_x}deg)`);
              if (element.rotate_y) transforms.push(`rotateY(${element.rotate_y}deg)`);
              if (element.rotate_z) transforms.push(`rotateZ(${element.rotate_z}deg)`);
              if (element.translate_x) transforms.push(`translateX(${element.translate_x})`);
              if (element.translate_y) transforms.push(`translateY(${element.translate_y})`);
              if (element.skew_x) transforms.push(`skewX(${element.skew_x}deg)`);
              if (element.skew_y) transforms.push(`skewY(${element.skew_y}deg)`);
              if (element.skew_z) transforms.push(`skewZ(${element.skew_z}deg)`);
              declaration += `transform: ${transforms.join(' ')}; `;
            }

            if (items[itemId].type === "noahs_background_image") {
              let backgroundImage = "";
              try {
                backgroundImage = await getBackgroundImage(element);
              } catch (error) {
                console.error("Error obteniendo el fondo:", error);
              }
              declaration += backgroundImage;
            }

            if (items[itemId].type === 'noahs_background_gradient') {
              let background = getBackgroundGradient(element);
              if (background) declaration += background;
            }

            if (items[itemId].type === 'noahs_background_overlay') {
              let background = getBackgroundOverlay(element);
              if (background) declaration += background;
            }


            if (declaration) {
              selectors[selector] = selectors[selector] || [];
              selectors[selector].push(declaration);
            }
          }
        }

        if (items[itemId] && items[itemId].type === 'noahs_divider') {
          for (const [k, value] of Object.entries(element)) {
            let selector = '#widget-id-' + wid + ' .noahs-divider-svg.' + k + ' svg';
            let declaration = dividerStyles(k, value);
            css += selector + '{' + declaration + '}';
          };
        }

        if (items[itemId] && items[itemId].type === 'noahs_custom_css') {
          css += element.replaceAll('selector', '#widget-id-' + wid);
        }
      }

      Object.entries(selectors).forEach(([selector, declarations]) => {
        css += `${selector} { ${declarations.join(' ')} } `;
      });

      return css;
    }


    async function getBackgroundImage(value) {
      let css = "";

      if (!value) return css;

      // Verificar si hay un token de imagen
      if (value.token_media) {
        try {
          // Suponiendo que hay un endpoint en Drupal para procesar tokens
          const response = await fetch(`/noahs-admin/get-image-token/${encodeURIComponent(value.token_media)}`);
          if (response.ok) {
            const data = await response.json();
            if (data.rendered_token) {
              css += `background-image: url(${data.rendered_token});`;
            }
          }
        } catch (error) {
          console.error("Error obteniendo el token de la imagen:", error);
        }
      }


      if (value.background_image?.url) {
        css += `background-image: url(${value.background_image.url});`;
      
        css += `background-repeat: ${value.background_image.background_repeat || 'no-repeat'};`;
        css += `background-position: ${value.background_image.background_position || 'center center'};`;
        css += `background-attachment: ${value.background_image.background_attachment || 'scroll'};`;
      
        if (value.background_size_custom) {
          css += `background-size: ${value.background_size_custom};`;
        } else {
          css += `background-size: ${value.background_image.background_size || 'cover'};`;
        }
      }

      return css;
    }

    function dividerStyles(k, divider, principalColor = "--noahs_page_builder-principal-color") {
      let css = "";

      if (divider?.divider) {
        css += `width:${divider.width ?? 100}%;`;
        css += `height:${divider.height ?? 300}px;`;
        css += `fill:${divider.color ?? principalColor};`;

        if (divider.align) {
          if (divider.align === "center") css += "margin:auto;";
          if (divider.align === "left") css += "margin-right:auto;";
          if (divider.align === "right") css += "margin-left:auto;";
        }

        let transform = "";
        if (k === "bottom") {
          transform += "rotate(180deg)";
          if (divider.direction) transform += " scaleX(-1)";
        }
        if (k === "top" && divider.direction) {
          transform += "scaleX(-1)";
        }
        if (transform) {
          css += `transform:${transform};`;
        }
      }

      return css;
    }

    function getBackgroundGradient(value) {
      let css = "";

      if (value?.gradient?.type) {
        const { start, end, type } = value.gradient;
        let startColor = start?.color ?? "transparent";
        let endColor = end?.color ?? "transparent";
        const startLocation = start?.location ?? 0;
        const endLocation = end?.location ?? 100;

        const find_start_varible = Object.values(variables_color).includes(startColor);
        if(find_start_varible) {
          const start_varible = Object.entries(variables_color).find(([k, v]) => v === startColor)?.[0];
          startColor = `var(${start_varible}) `;
        }
        
        const find_end_varible = Object.values(variables_color).includes(endColor);
        if(find_end_varible) {
          const end_varible = Object.entries(variables_color).find(([k, v]) => v === endColor)?.[0];
          endColor = `var(${end_varible}) `;
        }

        const gradients = {
          horizontal: `linear-gradient(to right, ${startColor} ${startLocation}%, ${endColor} ${endLocation}%)`,
          vertical: `linear-gradient(to bottom, ${startColor} ${startLocation}%, ${endColor} ${endLocation}%)`,
          diagonal: `linear-gradient(135deg, ${startColor} ${startLocation}%, ${endColor} ${endLocation}%)`,
          "diagonal-bottom": `linear-gradient(45deg, ${startColor} ${startLocation}%, ${endColor} ${endLocation}%)`,
          radial: `radial-gradient(ellipse at center, ${startColor} ${startLocation}%, ${endColor} ${endLocation}%)`,
        };

        if (gradients[type]) {
          css += `background: ${gradients[type]};`;
        }
      }

      return css;
    }

    function getBackgroundOverlay(value) {
      let css = "";

      if (value?.overlay?.type) {
        const { start, end, type } = value.overlay;
        let startColor = start?.color ?? "transparent";
        let endColor = end?.color ?? "transparent";
        const startLocation = start?.location ?? 0;
        const endLocation = end?.location ?? 100;

        const find_start_varible = Object.values(variables_color).includes(startColor);
        if(find_start_varible) {
          const start_varible = Object.entries(variables_color).find(([k, v]) => v === startColor)?.[0];
          startColor = `var(${start_varible}) `;
        }
        
        const find_end_varible = Object.values(variables_color).includes(endColor);
        if(find_end_varible) {
          const end_varible = Object.entries(variables_color).find(([k, v]) => v === endColor)?.[0];
          endColor = `var(${end_varible}) `;
        }


        if (type === "solid") {
          css += `background-color:${startColor};`;
        } else {
          const gradients = {
            horizontal: `linear-gradient(to right, ${startColor} ${startLocation}%, ${endColor} ${endLocation}%)`,
            vertical: `linear-gradient(to bottom, ${startColor} ${startLocation}%, ${endColor} ${endLocation}%)`,
            diagonal: `linear-gradient(135deg, ${startColor} ${startLocation}%, ${endColor} ${endLocation}%)`,
            "diagonal-bottom": `linear-gradient(45deg, ${startColor} ${startLocation}%, ${endColor} ${endLocation}%)`,
            radial: `radial-gradient(ellipse at center, ${startColor} ${startLocation}%, ${endColor} ${endLocation}%)`,
          };

          if (gradients[type]) {
            css += `background: ${gradients[type]};`;
          }
        }
      }

      css += `content:""; position:absolute; top:0; left:0; width:100%; height:100%;`;
      return css;
    }

    // Get widget styles
    async function generateWidgetStyles(settings) {
      let output = '';

      if (!settings || !settings.element) {
        return output;
      }

      if (element_fields && Object.keys(settings.element).length) {

        let css = obtenerCss(settings.element);

        if (css) {
          let item_type = settings.type;
          output = await getStyles(element_fields[item_type], css, settings.wid);
        }
      }

      return output;
    }

    // Get page styles
    async function generatPageStyles(settings, pageSettings) {
      let output = '';

      if (!settings || !settings.element) {
        return output;
      }

      if (pageSettings && Object.keys(settings.element).length) {

        let css = obtenerCss(settings.element);

        if (css) {
          output = await getStyles(pageSettings, css, 'no_id');
        }
      }

      return output;
    }

    async function obtenerThemeCss(settings) {
      settings = settings.page;
      let css = '';
      if (settings.site_logo_width) {
        css += '.noahs-pro-theme--header.sticky img.site__logo{max-width:' + settings.site_logo_width + ' !important;}';
      }
      if (settings.background_color_scroll) {
        css += '.noahs-pro-theme--header.sticky {background-color:' + settings.background_color_scroll + ' !important;}';
      }
      if (settings.background_color) {
        css += 'body{background-color:' + settings.background_color + ' !important;}';
      }
      if (settings.header_background_color) {
        css += 'header{background-color:' + settings.header_background_color + ' !important;}';
      }

      return css;
    }

  })(jQuery, Drupal, drupalSettings);

}