    // Obtener formulario del widget
    function addWidgetForm(widget_id, section_id, global, settings) {

      var data = {
        nid: drupalSettings.nid,
        widget: widget_id,
        section_id: section_id,
        global: global,
        settings: JSON.stringify(settings),
      };

      var result = "";
      $.ajax({
        url: '/noahs-admin/modal-form/' + drupalSettings.nid + '/' + widget_id + '/' + section_id,
        method: 'POST',
        data: JSON.stringify(data),
        async: false,
        success: function (html) {
          result = html.html;
        },
        complete: () => {
          setTimeout(function () {
            var contenidoIframe = $('#noahs_page_builder_preview').contents();
            contenidoIframe.find("#noahs_overlay").fadeOut(300);
          }, 500);
        },
        error: function (jqXHR, textStatus, errorThrown) {
          alert(textStatus + ":" + jqXHR.responseText);
        }
      });
      return result;
    }


