function closeNoahsMediaModal() {
  var $container = $('#noahs-media-container');

  if ($container.length) {

    // Desacoplar behaviors del contenedor
    try {
      Drupal.detachBehaviors($container.get(0));
    } catch (e) {
      console.warn('detachBehaviors error', e);
    }

    // Eliminar instancias de Drupal.ajax asociadas al modal
    // Views las guarda en data de los elementos form y paginacin
    $container.find('[data-drupal-selector]').each(function () {
      var $el = $(this);
      var drupalAjaxObj = $el.data('drupal-ajax');

      if (drupalAjaxObj) {
        try {
          drupalAjaxObj.abort();
        } catch (_) {}
        $el.removeData('drupal-ajax');
      }
    });

    // Eliminar eventos nuestros
    $(document).off('.noahsMedia');

    // Quitar modal
    $container.remove();
  }
}

// Media modal
function openMediaModal(element_id, source, type, total, wid) {

  // Construir URL
  var url = '/noahs-admin/noahs_page_builder/media-modal?' +
    'element_id=' + encodeURIComponent(element_id) + '&' +
    'type=' + encodeURIComponent(type) + '&' +
    'source=' + encodeURIComponent(source) + '&' +
    'wid=' + encodeURIComponent(wid);

  // Limpiar contenedor previo
  var $old = $('#noahs-media-container');
  if ($old.length) {
    $old.remove();
  }

  // Crear contenedor
  var $wrapper = $(`
    <div id="noahs-media-container" class="noahs-media-html">
      <button class="noahs-media-close btn-admin btn-danger" type="button">&times;</button>
      <div class="noahs-media-content">
        <div id="noahs-media-inner"></div>
      </div>
    </div>
  `);

  $('body').append($wrapper);

  // Cerrar modal
  $wrapper.on('click.noahsMedia', '.noahs-media-close', function () {
    closeNoahsMediaModal();
  });
  // Instanciar Ajax Drupal
  var drupalAjax = Drupal.ajax({
    url: url,
    submit: {
      element_id: element_id,
      type: type,
      source: source,
      wid: wid
    },
    base: false,
    element: false
  });

  // Override success para enganchar behaviors justo despus de ReplaceCommand
  var originalSuccess = drupalAjax.success;
  drupalAjax.success = function (response, status) {
    // Ejecutar lo que Drupal hace normalmente
    originalSuccess.call(this, response, status);

    // Ya est el contenido reemplazado
    var container = document.getElementById('noahs-media-container');
    if (container) {
      Drupal.attachBehaviors(container);
      initNoahsMediaEvents($wrapper, element_id, source, type, total, wid);
    }
  };

  // Ejecutar
  drupalAjax.execute();
}

// Inicializar eventos del modal
    function initNoahsMediaEvents($wrapper, element_id, source, type, total, wid) {
        var modalContent = $wrapper.find('.noahs_page_builder-media-modal');
        // Limpieza de eventos previos para evitar duplicacin
        $(document).off("click", '.image-box');
        $(document).off("click", '.insert-media-modal');

        var sourceData = modalContent.data('source');

        // Gestin de imgenes o fondos
        if (source === 'image' || source === 'background') {

          if (type === 'multiple') {

            // Seleccin mltiple
            $(document).on("click", '.image-box', function (e) {
              console.log('click multiple');
              $(this).toggleClass('selected');
            });

            $(document).on("click", '.insert-media-modal', function (e) {
              e.preventDefault();

              var selectedData = [];
              $('.image-box.selected').each(function () {
                selectedData.push({
                  mediaType: $(this).data('media-type'),
                  fileid: $(this).data('fileid'),
                  thumbnail: $(this).data('thumbnail')
                });
              });

              // Construir HTML
              var selectedHtml = '';
              var newItems = '';
              for (var i = 0; i < selectedData.length; i++) {
                var index = (total + i);
                selectedHtml += `<div class="col-3 image-box mb-2 position-relative" data-delta="${index}">
                              <div class="noahs_gallery_field_actions">
                                  <div class="noahs_page_builder-edit-grid-item btn btn-sm btn-info position-absolute top-50 start-50 translate-middle rounded-circle" data-show-click="#edit-gallery-image-${index}">
                                    <i class="fa-solid fa-pen-to-square"></i>
                                  </div>
                                  <div class="noahs_page_builder-move-grid-item btn btn-sm btn-info position-absolute top-0 start-0 rounded-circle ui-sortable-handle">
                                    <i class="fa-solid fa-arrows-up-down-left-right"></i>
                                  </div>
                                  <div class="noahs_page_builder-remove-grid-item btn btn-sm btn-danger position-absolute bottom-0 end-0 rounded-circle">
                                    <i class="fa-solid fa-trash"></i>
                                  </div>
                              </div>
                              <img src="${selectedData[i].thumbnail}" data-fid="${selectedData[i].fileid}" style="width:100%; height:auto;">
                              <div id="edit-gallery-image-${index}" class="noahs_page_builder-hover-modal bg-light p-3 position-absolute top-50 start-0 translate-middle-y shadow-lg hidden w-100">
                                  <input type="hidden" name="element[gallery_items][${index}][fid]" value="${selectedData[i].fileid}">
                                  <input class="form-control" type="text" name="element[gallery_items][${index}][url]" value="">
                              </div>
                          </div>`;
                newItems += `<div class="gallery-item col">
                              <a data-fancybox="gallery" href="${selectedData[i].thumbnail}">
                                  <img class="gallery-image-src" src="${selectedData[i].thumbnail}">
                              </a>
                          </div>`;
              }

              // Agregar HTML al contenedor
              $('#' + element_id).append(selectedHtml);
              // $('.update_data_form').trigger('change');
              var formData = $('#' + element_id).closest('form').find(':input').filter(function () {
                return this.value.trim().length > 0;
              }).serializeJSON();
              // $('#' + element_id).trigger('change');
              if (source === 'image') {
                regenerateWidget(wid, formData);
              }
              // Cerrar el modal
              closeNoahsMediaModal();
            });
          } else {
            // Seleccin nica
            $(document).on("click", '.image-box', function (e) {

              $('.image-box').removeClass('selected');
              $(this).addClass('selected');
              var mediaType = $(this).data('media-type');
              var fid = $(this).data('fileid');
              var thumbnail = $(this).data('thumbnail');
              $('.insert-media-modal').attr('data-media-type', mediaType).attr('data-fileid', fid).attr('data-thumbnail', thumbnail);

            });

            // insertar imagen o fondo en el formulario
            $(document).on("click", '.insert-media-modal', function (e) {
              e.preventDefault();
              
              var mediaType = $(this).data('media-type');
              var fid = $(this).data('fileid');
              var thumbnail = $(this).data('thumbnail');
              var image_style = 'original';
              console.log('Inserting media:', { mediaType, fid, thumbnail });
              $('#' + element_id).val(fid);
              $('#' + element_id).parent().find('.background-thumbnail-image').attr('src', thumbnail).show();
              $('#' + element_id).parent().find('.background-thumbnail').val(thumbnail);
              $('#' + element_id).parent().find('.background-media-type').val(mediaType);

              if (source === 'image') {
                var formData = $('#' + element_id).closest('form').find(':input').filter(function () {
                  return this.value.trim().length > 0;
                }).serializeJSON();
                regenerateWidget(wid, formData);
              }else{
                $.ajax({
                  url: `/noahs-admin/get-media-image/${mediaType}/${fid}/${image_style}`,
                  type: 'GET',
                  processData: false,
                  contentType: false,
                  success: function (data) {
                    console.log(data);
                    $('#' + element_id).parent().find('.background-url').val(data);
  
                    var formData = $('#' + element_id).closest('form').find(':input').filter(function () {
                      return this.value.trim().length > 0;
                    }).serializeJSON();
                    $('#' + element_id).trigger('change');
       
                  }
                });
 
              }
              // Cerrar el modal
              closeNoahsMediaModal();

            });
          }
        }

        // Gestin de videos
        if (source === 'video') {
          $(document).on("click", '.image-box', function (e) {
            e.preventDefault();
            $('.image-box').removeClass('selected');
            $(this).addClass('selected');
            var fid = $(this).data('fileid');
            var thumbnail = $(this).data('thumbnail');
            $('.insert-media-modal').attr('data-fileid', fid).attr('data-thumbnail', thumbnail);
          });

          $(document).on("click", '.insert-media-modal', function (e) {
            e.preventDefault();

            var fid = $(this).data('fileid');
            var thumbnail = $(this).data('thumbnail');
            $('#' + element_id).val(fid);
            $('#' + element_id).parent().find('.noahs-video-iframe source').attr('src', thumbnail);
            $('#' + element_id).parent().find('.noahs-video-iframe')[0].load();

            var formData = $('#' + element_id).closest('form').find(':input').filter(function () {
              return this.value.trim().length > 0;
            }).serializeJSON();

            // Cerrar el modal
            closeNoahsMediaModal();
          });
        }

        // Subir archivos
        $(document).off('change', '#noahs_page_builder_upload_image')
           .on('change', '#noahs_page_builder_upload_image', function () {
          var files = $(this).prop("files");

          if (!files || files.length === 0) {
            $('.modal-messages').append('<p>Por favor, selecciona al menos un archivo</p>');
            return;
          }

          var formData = new FormData();

          for (var i = 0; i < files.length; i++) {
            var fileInput = files[i];

            if (source === 'video') {
              // Verificar si el archivo es una video WEBM
              if (fileInput.type !== 'video/mp4' && fileInput.type !== 'video/webm') {
                $('.modal-messages').append('<p>Por favor, selecciona un archivo mp4 o webm</p>');
                return;
              }
            }

            if (source === 'image') {
              if (fileInput.type !== 'image/jpeg' && fileInput.type !== 'image/png' && fileInput.type !== 'image/gif') {
                $('.modal-messages').append('<p>Por favor, selecciona un archivo JPG o PNG</p>');
                return;
              }
            }

            formData.append('files[' + i + ']', fileInput);
          }

          $.ajax({
            url: '/noahs-admin/noahs_page_builder/upload_file',
            type: 'POST',
            data: formData,
            processData: false,
            contentType: false,
            beforeSend: function ($data) {
              $('.noahs_page_builder-media-modal').prepend('<div class="noahs_page_builder_loading"><div class="spinner-grow text-primary" role="status"><span class="visually-hidden">Loading...</span></div></div>');
            },
            success: function (data) {

              $('#fileInput').val(null)
              var files = data.files;
              for (var i = 0; i < files.length; i++) {
                var fileData = files[i];
                if (source === 'image' || source === 'background') {

                  $('.noahs_page_builder-modal_container .noahs-new-media-element').prepend('<div class="col-2 image-box mb-2 selected" data-media-type="noahs_image" data-fileid="' + fileData.media_id + '" data-thumbnail="' + fileData.file_url + '"><span><img src="' + fileData.file_url + '"></span></div>');
                }
                if (source === 'video') {
                  $('.noahs_page_builder-modal_container .noahs-new-media-element').prepend('<div class="col-2 image-box mage-box-video mb-2 selected" data-video-url="' + fileData.file_fullurl + '" data-fileid="' + fileData.media_id + '"><video class="noahs-video-iframe" width="100%"><source src="' + fileData.file_fullurl + '" type="' + fileData.file_name + '"><p>Su navegador no soporta vdeos HTML5.</p></video></div>');
                }
                $('.insert-media-modal').attr('data-media-type', fileData.media_type).attr('data-fileid', fileData.media_id).attr('data-thumbnail', fileData.file_fullurl);
              }
              $('.modal-messages').append('<div class="alert alert-success mt-3" role="alert"><i class="fa-solid fa-circle-check me-3"></i>' + Drupal.t('Files uploaded successfully') + '</div>');

              $('.image-box').on('click', function (e) {
                e.preventDefault();
                $('.image-box').removeClass('selected');
                $(this).addClass('selected');
                var fid = $(this).data('fileid');
                var mediaType = $(this).data('media-type');
                var thumbnail = $(this).data('thumbnail');
                $('.insert-media-modal').attr('data-media-type', mediaType).attr('data-fileid', fid).attr('data-thumbnail', thumbnail);
              });
            },
            complete: function ($data) {
              $('.noahs_page_builder_loading').remove();
            },
            error: function (error) {
              console.log(error);
              $('.modal-messages').addClass('error').append('<div class="alert alert-danger" role="alert"><i class="fa-solid fa-circle-exclamation me-3"></i>' + error.responseJSON.message + '</div>');
            }
          });
        });

    }


